import '../Css/LandingPage.css';
import React from 'react';
import { GithubOutlined, ArrowRightOutlined } from '@ant-design/icons';
import {  Button, Modal, Image } from 'antd';

function ProjectItem({ index, theme, title, alt, path, description, link, website, projectImgs}) {
  const [open, setOpen] = React.useState(false);
  const showModal = () => {
    setOpen(true);
  };
  const handleCancel = () => {
    setOpen(false);
  };

  const buttonArray = link && !website ? [<Button key="link" href={link} type="primary" target="_blank" rel="noreferrer"><GithubOutlined />  View Code</Button>, ] : [<Button key="link" href={link} target="_blank" rel="noreferrer"><GithubOutlined /> View Code</Button>, <Button key="link" href={website} type="primary" target="_blank" rel="noreferrer"><ArrowRightOutlined /> Visit Website</Button> ];

  const renderModalImgs = projectImgs?.map(img => {
    return (<Image src={img} preview={false}></Image>);
  })

  return (
    <>
      <div className={theme} onClick={showModal}>
        <img className='card-img' src={path} alt={alt} />
        <div>
          <h4 className='card-title card-left'>{title}</h4>
          <br></br>
          <p className='card-left web-description'>{description}</p>
          <br></br>
        </div>
      </div>
        <Modal
          open={open}
          title={title}
          onCancel={handleCancel}
          footer={[...buttonArray]}
        >
          <p>{description}</p>
          {renderModalImgs}
        </Modal>
    </>
  );
}

export default ProjectItem;
